		return {
			endYear: new Date(),
			current: 0,
			buyTime: '',
			changeTime: '',
			YZM:'',
			phone:''
		}
	},
	components: {
		wPicker
	},
	onLoad() {

	},
	methods: {
		choseTime(n) {
			this.current = n;
			this.$refs.date.show()
		},
